* {
  box-sizing: border-box;
}

body{
  text-align: center;
}

.App{
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  transition: 0.3s;
  img{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: auto;
  }
  h1 {
    position: relative;
    width: 300px;
    color: white;
    font-weight: 300;
    margin: 50px auto;
  }
}